import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, ViewController } from 'ionic-angular';
import { HospitalMapper } from '../../../models/hospital.model';
import { HospitalsProvider } from '../../../providers/hospitals/hospitals';

/**
 * Generated class for the HospitalSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'HospitalSearchPage'
})
@Component({
  selector: 'page-hospital-search',
  templateUrl: 'hospital-search.html',
})
export class HospitalSearchPage {

  searching: boolean = false;

  hospitalName: string;

  currentItems: HospitalMapper[];

  originalItems: HospitalMapper[];

  hospitalSelected: HospitalMapper;

  constructor(public navCtrl: NavController, private viewCtrl: ViewController,
              public hospitals: HospitalsProvider, public modalCtrl: ModalController) {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad HospitalSearchPage');
    this.initializeItems();
  }

  initializeItems() {
    this.searching = true;
    this.hospitals.query().subscribe((data: HospitalMapper[]) => {
      this.searching = false;
      this.originalItems = data;
      this.currentItems = this.originalItems;
    });
  }

  onCancel() {
    this.currentItems = this.originalItems;
  }

  getHospitals(ev: any) {
    const val = ev.target.value;
    // Reset items back to all of the items
    if (!this.originalItems || !this.originalItems.length) {
      return;
    }

    if (val === '' ) {
      this.currentItems = this.originalItems;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.currentItems = this.originalItems.filter((item: any) => {
        return item.mapper.name.toLowerCase().indexOf(val.toLowerCase()) >= 0;
      })
    }
  }

  selectHospital(item: HospitalMapper) {
    this.hospitalSelected = item;
  }

  dismiss() {
    this.viewCtrl.dismiss({ hospital: this.hospitalSelected });
  }

}



